<template>
  <link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet">
    <div class="page-container">
      <div class="import-controls-container">
        <div class="controls-row">
          <div class="import-controls">
            <label class="import-btn">
              <input type="file" accept=".json,.h" @change="importAreaInfo" class="file-input">
              <span class="material-icons">upload_file</span>
              Import
            </label>
            <label class="import-btn" v-if="imagePath">
              <input type="file" 
                webkitdirectory 
                directory 
                @change="importImages" 
                class="file-input">
              <span class="material-icons">drive_folder_upload</span>
              Load Images
            </label>
          </div>
          <div class="image-selector">
            <!-- <input type="file" accept="image/*" @change="handleImageUpload" ref="fileInput" class="file-input"> -->
            <label class="import-btn">
              <input type="file" accept="image/*" @change="handleImageUpload" ref="fileInput" class="file-input">
              <span class="material-icons">file_open</span>
              Select BG
            </label>
            <!-- <button @click="triggerFileInput" class="select-image-btn">
              Select Image
            </button> -->
            <label class="switch" v-if="selectedImage">
              <span class="switch-label">Edit Mode</span>
              <input type="checkbox" v-model="isEditing">
              <span class="slider round"></span>        
            </label>
            
          </div>
        </div>
      </div>
      
      <div class="image-section">
        <div class="image-container">          
          <span class="file-info" v-if="BGName">
            <span class="material-icons">description</span>
            {{ BGName }}
          </span>
          <p> </p>
          <div v-if="selectedImage" class="image-display">
            <img 
              :src="selectedImage" 
              useMap="#workMap" 
              class="image-map"
              @load="initializeCanvas"
              @click="handleImageClick"
              @mousedown.prevent="startDrawing"
              @mousemove.prevent="drawing"
              @mouseup.prevent="endDrawing"
              draggable="false"
            >
            <map name="workMap" v-if="!isEditing">
              <area v-for="area in areas"
                    :key="area.id"
                    :shape="area.shape"
                    :coords="area.coords"
                    :href="area.href"
                    :alt="area.alt"
                    @click.prevent
                    @mouseover="highlightArea(area.id)"
                    @mouseout="removeHighlight()">
            </map>

            <!-- 영역 배경 이미지 추가 -->
            <div v-for="area in areas" 
                :key="'bg-'+area.id" 
                class="area-background"
                v-show="area.visible && area.imageUrl"
                :style="getAreaBackgroundStyle(area)">
              <img v-if="area.imageUrl" :src="area.imageUrl" alt="Area background">
            </div>

            <canvas 
              ref="highlightCanvas" 
              class="highlight-canvas"
              @click="handleCanvasClick($event)"
              :style="{ cursor: isEditing ? 'pointer' : 'default' }">
            </canvas>
            
            <!-- Area Numbers -->
            <div v-for="area in areas" 
                :key="area.id" 
                class="area-number"
                :class="{ 'selected': selectedAreaId === area.id }"
                
                :style="getAreaNumberStyle(area)">
              {{ area.id }}
            </div>
          </div>
        </div>

        <!-- Modern Table for Area Coordinates -->
        <div v-if="areas.length > 0" class="area-table-container">
          <h3>Area Info</h3>
          <div class="import-controls">
            <div class="image-path-input">
              <input 
                type="text" 
                id="imagePath" 
                v-model="imagePath" 
                placeholder="Enter image path"
                class="path-input"
              >
            </div>
            
          </div>

          <table class="modern-table">
            <thead>
              <tr>
                <th>No.</th>
                <th>X</th>
                <th>Y</th>
                <th>Width</th>
                <th>Height</th>
                <th>Image</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="area in areas" :key="area.id" :class="{ 'editing-row': isEditingArea(area.id) }">
                <td class="area-id">{{ area.id }}</td>
                <td>
                  <input 
                    type="number" 
                    v-model.number="getAreaCoords(area).x"                
                    @input="updateCoord(area.id, 'x', $event.target.value)"
                    class="coordinate-input"
                  >
                </td>
                <td>
                  <input 
                    type="number" 
                    v-model.number="getAreaCoords(area).y"
                    @input="updateCoord(area.id, 'y', $event.target.value)"
                    class="coordinate-input"
                  >
                </td>
                <td>
                  <input 
                    type="number" 
                    v-model.number="getAreaCoords(area).width"
                    @input="updateCoord(area.id, 'width', $event.target.value)"
                    class="coordinate-input"
                    :disabled="area.imageUrl"
                    :title="area.imageUrl ? 'If an image is set, you can\'t change its width' : ''"
                  >
                </td>
                <td>
                  <input 
                    type="number" 
                    v-model.number="getAreaCoords(area).height"
                    @input="updateCoord(area.id, 'height', $event.target.value)"
                    class="coordinate-input"
                    :disabled="area.imageUrl"
                    :title="area.imageUrl ? 'If an image is set, you can\'t change its height' : ''"
                  >
                </td>
                <td>
                  <div class="file-upload-container">
                    <label class="custom-file-upload" :title="'Select an image for ' + area.id">
                      <input 
                        type="file" 
                        accept="image/*"
                        @change="handleAreaImageUpload($event, area.id)"
                        class="image-upload"
                      >
                      <span class="material-icons">image</span>
                    </label>
                    <span class="selected-filename" v-if="areaFilenames[area.id]">
                      {{ areaFilenames[area.id] }}
                    </span>
                  </div>
                  <!-- <img 
                    v-if="area.imageUrl" 
                    :src="area.imageUrl" 
                    class="area-thumbnail"
                    alt="Area image"
                  > -->
                </td>
                <td class="actions-cell">
                  <button @click="toggleVisibility(area.id)" class="action-btn visibility" :title="area.visible ? 'Hide' : 'Show'">
                    <span class="material-icons">{{ area.visible ? 'visibility' : 'visibility_off' }}</span>
                  </button>
                  <button @click="deleteArea(area.id)" class="action-btn delete" :title="'Remove area'">
                    <span class="material-icons">clear</span>
                  </button>
                </td>
              </tr>
            </tbody>
          </table>
          <p ></p>

          <div class="export-buttons">
            <button @click="exportAsJson" class="export-btn">Save to JSON </button>
            <button @click="exportAsCStruct" class="export-btn">Save to C Struct</button>
          </div>
        </div>

        
      </div>
    </div>
</template>

<script>
export default {
  name: 'ImageMap',
  data() {
    return {
      selectedImage: null,
      areas: [],
      areaFilenames: {}, // 영역별 실제 파일명 저장
      isEditing: true,
      isDrawing: false,
      startX: 0,
      startY: 0,
      currentArea: null,
      editingAreaId: null,
      isDragging: false,
      selectedAreaId: null,
      dragStartX: 0,
      dragStartY: 0,
      imagePath: '',
      BGName: '',
      BGSize: ''
    }
  },
  methods: {
    toggleVisibility(areaId) {
      const area = this.areas.find(a => a.id === areaId);
      if (area) {
        area.visible = !area.visible;
        this.drawSavedAreas();
      }
    },

    handleCanvasClick(event) {
      if (!this.isEditing) return;
      
      const rect = event.target.getBoundingClientRect();
      const scale = event.target.width / event.target.clientWidth;
      const x = (event.clientX - rect.left) * scale;
      const y = (event.clientY - rect.top) * scale;

      // 클릭한 위치에 있는 영역 찾기
      const clickedArea = this.areas.find(area => {
        const [x1, y1, x2, y2] = area.coords.split(',').map(Number);
        return x >= x1 && x <= x2 && y >= y1 && y <= y2;
      });

      if (clickedArea) {
        this.selectedAreaId = clickedArea.id;
        this.highlightArea(clickedArea.id);
      } else {
        this.selectedAreaId = null;
        this.drawSavedAreas();
      }
    },
    selectArea(areaId) {
      this.selectedAreaId = areaId;
    },
    
    // 이미지 클릭 시 선택 해제
    handleImageClick(event) {
      if (event.target.classList.contains('image-map')) {
        this.selectedAreaId = null;
      }
    },

    importImages(event) {
      const files = Array.from(event.target.files);
      
      // Create a map of filenames to their data URLs
      const imageMap = {};
      
      files.forEach(file => {
        if (file.type.startsWith('image/')) {
          const reader = new FileReader();
          reader.onload = (e) => {
            imageMap[file.name] = e.target.result;
            
            // Set background image if filename matches
            if (file.name === this.BGName) {
              this.selectedImage = e.target.result;
            }
            
            // Set area images if filename matches
            this.areas.forEach(area => {
              if (this.areaFilenames[area.id] === file.name) {
                area.imageUrl = e.target.result;
              }
            });
          };
          reader.readAsDataURL(file);
        }
      });
    },

    importAreaInfo(event) {
      const file = event.target.files[0];
      if (!file) return;

      const reader = new FileReader();
      reader.onload = async(e) => {
        const content = e.target.result;
        
        if (file.name.endsWith('.json')) {
          await this.importFromJson(content);
        } else if (file.name.endsWith('.h')) {
          await this.importFromCStruct(content);
        }

        
      };
      reader.readAsText(file);
    },

    clearAllAreas() {
      this.areas = [];
      this.areaFilenames = {}; // 관련 파일명 정보도 함께 지우기
      //this.drawSavedAreas(); // 캔버스 다시 그리기
    },

    importFromJson(content) {
      try {
        const data = JSON.parse(content);

        // Set image path if available
        this.imagePath = data.imagePath || '';
        this.BGName = data.BGName || '';

        // Check if data is in the new format (with imagePath) or old format (just array of areas)
        let areasData;

        if (Array.isArray(data)) {
          // Old format - direct array of areas
          areasData = data;
          this.imagePath = ''; // Reset image path
        } else if (data && typeof data === 'object') {
          // New format with imagePath
          this.imagePath = data.imagePath || '';
          
          // Check if areas property exists and is an array
          if (data.areas && Array.isArray(data.areas)) {
            areasData = data.areas;
          } else {
            // Try to use the whole object as area data
            areasData = [data];
          }
        } else {
          throw new Error('Invalid JSON structure');
        }

        this.clearAllAreas();
        
        // Process the areas data
        this.areas = areasData.map(item => {
          // Make sure coordinates exist
          if (!item.coordinates) {
            console.error('Missing coordinates in area:', item);
            return null;
          }
          
          return {
            id: item.id || 0,
            shape: 'rect',
            coords: `${item.coordinates.x},${item.coordinates.y},${item.coordinates.x + item.coordinates.width},${item.coordinates.y + item.coordinates.height}`,
            href: '#',
            alt: `Area ${item.id || 0}`,
            imageUrl: null,
            visible: item.visible !== undefined ? item.visible : true
          };
        }).filter(area => area !== null); // Remove any null entries
        
        // 파일명 정보 복원
        this.areaFilenames = {};
        areasData.forEach(item => {
          if (item.id && item.filename) {
            this.areaFilenames[item.id] = item.filename;
          }
        });

        this.$nextTick(() => {
          if (this.$refs.highlightCanvas && this.selectedImage)
           {
            this.drawSavedAreas();
          }
        });
        
        //console.log('Successfully imported JSON data:', { areas: this.areas, imagePath: this.imagePath });
      } catch (error) {
        console.error('Error importing JSON:', error);
        alert('The JSON file format is not correct:' + error.message);
      }
    },

    importFromCStruct(content) {
      try {
        // Extract image path if available
        const imagePathMatch = content.match(/#define IMAGE_PATH "([^"]*)"/) || 
                              content.match(/\/\/ Image Path: (.+)/);
        if (imagePathMatch && imagePathMatch[1]) {
          this.imagePath = imagePathMatch[1];
        }

        const BGNameMatch = content.match(/#define BGName "([^"]*)"/) || 
                              content.match(/\/\/ BG Name: (.+)/);
        if (BGNameMatch && BGNameMatch[1]) {
          this.BGName = BGNameMatch[1];
        }

        // C struct 파싱을 위한 정규식 수정
        const areaRegex = /{\s*(\d+)\s*,\s*(\d+)\s*,\s*(\d+)\s*,\s*(\d+)\s*,\s*(\d+)\s*,\s*"([^"]*)"\s*,\s*(\d+)\s*}/g;
        //const areas = [];
        let match;

        this.clearAllAreas();

        while ((match = areaRegex.exec(content)) !== null) {
          const id = parseInt(match[1]);
          const x = parseInt(match[2]);
          const y = parseInt(match[3]);
          const width = parseInt(match[4]);
          const height = parseInt(match[5]);
          const filename = match[6];
          const visible = match[7] === "1"; // 1이면 true, 0이면 false
          
          this.areas.push({
            id: id,
            shape: 'rect',
            coords: `${x},${y},${x + width},${y + height}`,
            href: '#',
            alt: `Area ${id}`,
            imageUrl: null,
            visible: visible
          });          
          
          // 파일명 정보 저장
          if (filename) {
            this.areaFilenames[id] = filename;
          }

          console.log('Imported area:', { id, x, y, width, height, filename, visible });
        }

        // 영역이 하나도 매칭되지 않았을 경우 로그 출력
        if (this.areas.length === 0) {
          console.error('No areas found in the C struct file. Content:', content);
          alert('No valid areas found in the C struct file.');
          return;
        }

        // 캔버스가 준비되었을 때만 drawSavedAreas 호출
        this.$nextTick(() => {
          if (this.$refs.highlightCanvas && this.selectedImage) {
            this.drawSavedAreas();
          }
        });
        console.log('Successfully imported C struct data:', this.areas);
      } catch (error) {
        console.error('Error importing C struct:', error);
        alert('The C Struct file format is not correct: ' + error.message);
      }
    },
    
    exportAsJson() {      
      const exportData = {
        imagePath: this.imagePath,
        BGName: this.BGName,
          areas: this.areas.map(area => ({
          id: area.id,
          coordinates: this.getAreaCoords(area),
          filename: this.areaFilenames[area.id] || '',
          visible: area.visible
        }))
      };
            
      const blob = new Blob([JSON.stringify(exportData, null, 2)], { type: 'application/json' });
      this.downloadFile(blob, 'image_map_areas.json');
    },
  
    exportAsCStruct() {
      let structContent = '// Image Path: ' + this.imagePath + '\n';
      structContent += '// BG Name: ' + this.BGName + '\n\n';
      structContent += 'typedef struct {\n';
      structContent += '    int id;\n';
      structContent += '    int x;\n';
      structContent += '    int y;\n';
      structContent += '    int width;\n';
      structContent += '    int height;\n';
      structContent += '    char filename[256];\n';
      structContent += '    int visible;\n';
      structContent += '} Area;\n\n';
      
      structContent += '#define IMAGE_PATH "' + this.imagePath + '"\n';
      structContent += '#define BGName "' + this.BGName + '"\n\n';
      structContent += 'Area areas[] = {\n';
      this.areas.forEach(area => {
        const coords = this.getAreaCoords(area);
        const filename = this.areaFilenames[area.id] || '';
        structContent += `    {${area.id}, ${coords.x}, ${coords.y}, ${coords.width}, ${coords.height}, "${filename}", ${area.visible ? 1 : 0}},\n`;
      });
      structContent += '};\n';
      
      const blob = new Blob([structContent], { type: 'text/plain' });
      this.downloadFile(blob, 'image_map_areas.h');
    },
    
    downloadFile(blob, filename) {
      const link = document.createElement('a');
      link.href = URL.createObjectURL(blob);
      link.download = filename;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },

    getAreaBackgroundStyle(area) {
      const coords = this.getAreaCoords(area)
      return {
        position: 'absolute',
        left: `${coords.x}px`,
        top: `${coords.y}px`,
        width: `${coords.width}px`,
        height: `${coords.height}px`,
        overflow: 'hidden'
      }
    },

    triggerFileInput() {
      this.$refs.fileInput.click()
    },

    formatFileSize(bytes){
      const units = ['B', 'KB', 'MB', 'GB']
      let size = bytes
      let unitIndex = 0
      
      while (size >= 1024 && unitIndex < units.length - 1) {
        size /= 1024
        unitIndex++
      }
      
      return `${size.toFixed(1)} ${units[unitIndex]}`
    },

    handleImageUpload(event) {
      const file = event.target.files[0]
      if (file && file.type.startsWith('image/')) {
        //console.log('file path : %s', file.name)
        this.BGName = file.name
        this.BGSize = this.formatFileSize(file.size)

        const reader = new FileReader()
        reader.onload = (e) => {
          this.selectedImage = e.target.result
          this.$emit('image-selected', file)
        }
        reader.readAsDataURL(file)
        this.isEditing = true
      }
    },
    toggleEditMode() {
      this.isEditing = !this.isEditing
    },
    drawSavedAreas() {
      const canvas = this.$refs.highlightCanvas
      const ctx = canvas.getContext('2d')
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      
      this.areas.forEach(area => {
        const [x1, y1, x2, y2] = area.coords.split(',').map(num => Math.round(Number(num)))
        const width = x2 - x1
        const height = y2 - y1
        
        // 흰색 테두리
        ctx.strokeStyle = 'white'
        ctx.lineWidth = 2
        ctx.setLineDash([])
        ctx.strokeRect(x1, y1, width, height)
        
        // 검은색 점선 테두리
        ctx.strokeStyle = 'black'
        ctx.lineWidth = 1
        ctx.setLineDash([4, 4])
        ctx.strokeRect(x1, y1, width, height)
      })
      
      ctx.setLineDash([]) // 대시 패턴 초기화
    },
    startDrawing(event) {
      if (!this.isEditing) return
      
      const rect = event.target.getBoundingClientRect()
      const scale = event.target.naturalWidth / event.target.clientWidth
      
      this.startX = Math.round((event.clientX - rect.left) * scale)
      this.startY = Math.round((event.clientY - rect.top) * scale)
      this.isDrawing = true
      
      this.currentArea = {
        startX: this.startX,
        startY: this.startY
      }
    },
    drawing(event) {
      if (!this.isDrawing || !this.isEditing) return

      const rect = event.target.getBoundingClientRect()
      const scale = event.target.naturalWidth / event.target.clientWidth
      
      const currentX = Math.round((event.clientX - rect.left) * scale)
      const currentY = Math.round((event.clientY - rect.top) * scale)

      const canvas = this.$refs.highlightCanvas
      const ctx = canvas.getContext('2d')
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      
      this.drawSavedAreas()
      
      const width = currentX - this.currentArea.startX
      const height = currentY - this.currentArea.startY
      
      // ctx.fillStyle = 'rgba(0, 0, 0, 0.3)'
      // ctx.fillRect(
      //   this.currentArea.startX,
      //   this.currentArea.startY,
      //   width,
      //   height
      // )
      // 반투명 채우기
      ctx.fillStyle = 'rgba(255, 255, 255, 0.3)'
      ctx.fillRect(
        this.currentArea.startX,
        this.currentArea.startY,
        width,
        height
      )
  
      // 이중 테두리 그리기 (흰색과 검은색)
      // 흰색 테두리
      ctx.strokeStyle = 'white'
      ctx.lineWidth = 2
      ctx.strokeRect(
        this.currentArea.startX,
        this.currentArea.startY,
        width,
        height
      )
      
      // 검은색 테두리 (점선)
      ctx.strokeStyle = 'black'
      ctx.lineWidth = 1
      ctx.setLineDash([4, 4])
      ctx.strokeRect(
        this.currentArea.startX,
        this.currentArea.startY,
        width,
        height
      )
      ctx.setLineDash([]) // 대시 패턴 초기화
    },
    handleAreaImageUpload(event, areaId) {
      const file = event.target.files[0]
      if (file && file.type.startsWith('image/')) {
        const reader = new FileReader()
        reader.onload = (e) => {
          const area = this.areas.find(a => a.id === areaId)
          // 이미지 로드 후 원본 크기 가져오기
          const img = new Image()
          img.onload = () => {
            const coords = this.getAreaCoords(area)
            area.imageUrl = e.target.result
            area.coords = `${coords.x},${coords.y},${coords.x + img.width},${coords.y + img.height}`
            this.areaFilenames[areaId] = file.name // 실제 파일명 저장
            
            this.drawSavedAreas()
          }
          img.src = e.target.result
        }
        reader.readAsDataURL(file)
      }
    },
    endDrawing(event) {
      if (!this.isDrawing || !this.isEditing) return

      const rect = event.target.getBoundingClientRect()
      const scale = event.target.naturalWidth / event.target.clientWidth
      
      const endX = Math.round((event.clientX - rect.left) * scale)
      const endY = Math.round((event.clientY - rect.top) * scale)

      if (Math.abs(endX - this.currentArea.startX) < 10 || 
          Math.abs(endY - this.currentArea.startY) < 10) {
        this.isDrawing = false
        this.drawSavedAreas()
        return
      }

      const newArea = {
        id: this.areas.length + 1,
        shape: 'rect',
        coords: `${this.currentArea.startX},${this.currentArea.startY},${endX},${endY}`,
        href: '#',
        alt: `Area ${this.areas.length + 1}`,
        imageUrl: null, // 이미지 URL 속성 추가
        visible: true
      }

      this.areas.push(newArea)
      this.isDrawing = false
      this.drawSavedAreas()
    },

    highlightArea(areaId) {
      const canvas = this.$refs.highlightCanvas
      const ctx = canvas.getContext('2d')
      
      this.drawSavedAreas()
      
      const area = this.areas.find(a => a.id === areaId)
      const [x1, y1, x2, y2] = area.coords.split(',').map(num => Math.round(Number(num)))
      const width = x2 - x1
      const height = y2 - y1
      
      // 반투명 채우기
      ctx.fillStyle = 'rgba(0, 255, 0, 0.3)'
      ctx.fillRect(x1, y1, width, height)
      
      // 흰색 테두리
      ctx.strokeStyle = 'white'
      ctx.lineWidth = 2
      ctx.setLineDash([])
      ctx.strokeRect(x1, y1, width, height)
      
      // 검은색 점선 테두리
      ctx.strokeStyle = 'black'
      ctx.lineWidth = 1
      ctx.setLineDash([4, 4])
      ctx.strokeRect(x1, y1, width, height)
      
      ctx.setLineDash([]) // 대시 패턴 초기화
    },

    removeHighlight() {
      this.drawSavedAreas()
    },

    initializeCanvas() {
      const img = event.target
      const canvas = this.$refs.highlightCanvas
      canvas.width = img.width
      canvas.height = img.height
      this.drawSavedAreas()
    },
    getAreaCoords(area) {
      const [x1, y1, x2, y2] = area.coords.split(',').map(Number)
      return {
        x: Math.min(x1, x2),
        y: Math.min(y1, y2),
        width: Math.abs(x2 - x1),
        height: Math.abs(y2 - y1)
      }
    },
    getAreaNumberStyle(area) {
      const coords = this.getAreaCoords(area)
      return {
        left: `${coords.x}px`,
        top: `${coords.y}px`
      }
    },
    deleteArea(areaId) {
      this.areas = this.areas.filter(area => area.id !== areaId)
      this.drawSavedAreas()
    },
    toggleEdit(areaId) {
      this.editingAreaId = this.editingAreaId === areaId ? null : areaId
    },
    isEditingArea(areaId) {
      return this.editingAreaId === areaId
    },

    updateCoord(areaId, type, value) {
        const area = this.areas.find(a => a.id === areaId)
        const coords = this.getAreaCoords(area)
        
        // 입력값이 숫자가 아니거나 음수인 경우 0으로 처리
        value = (!value || isNaN(value) || value < 0) ? 0 : parseInt(value)
        
        switch(type) {
        case 'x':
            area.coords = `${value},${coords.y},${value + coords.width},${coords.y + coords.height}`
            break
        case 'y':
            area.coords = `${coords.x},${value},${coords.x + coords.width},${value + coords.height}`
            break
        case 'width':
            area.coords = `${coords.x},${coords.y},${coords.x + value},${coords.y + coords.height}`
            break
        case 'height':
            area.coords = `${coords.x},${coords.y},${coords.x + coords.width},${coords.y + value}`
            break
        }
        
        this.drawSavedAreas()
    },

    // updateAreaCoords(areaId) {
    //     const area = this.areas.find(a => a.id === areaId)
    // const coords = this.getAreaCoords(area)
    // const newCoords = `${coords.x},${coords.y},${coords.x + coords.width},${coords.y + coords.height}`
    
    // // 영역 좌표 직접 업데이트
    // area.coords = newCoords
    
    // // 캔버스 다시 그리기
    // this.$nextTick(() => {
    //   this.drawSavedAreas()
    // })
    // }
  }
}
</script>

